.reservation {
    display: block;

    /* width: 99%; */
    width: 70em;
    height: fit-content;

    margin: 1px;


    border: 2px solid;
    border-radius: 3px;

    text-align: left;
}

.reservation:hover {
    cursor: pointer;
}

.reservation span {
    margin-left: 0.5em;
    display: inline-flex;
    overflow: hidden;
}


.timeVal {
    width: 1.5em;
}
.tarif{
    width: 5em;
}
.flyer{
    width: 15em;
}
.notes{
    width: 15em;
}
.id{
    width: 7em;
}
.payment{
    width: 7em;
}

.reservation-detailed {
    overflow: hidden;
    padding-left: 3em;
    text-align: left;
    font-style: italic;
}