.daysContainer {
    position: relative;
    display: block;
    overflow: scroll;

    width: 50em;
    height: 90vh;

    margin: 0.5em;


    border: 2px solid;
    border-radius: 3px;
}

.mainHeader {
    display: block;

    /* width: 99%; */
    width: 70em;
    height: fit-content;

    margin: 1px;


    border: 2px solid;
    border-radius: 3px;

    text-align: left;
    background-color: rgb(71, 108, 255);
}

.mainHeader span {
    margin-left: 0.5em;
    display: inline-flex;
    overflow: hidden;
}

.dayBlockDiv.mainHeader {
    position: sticky;
    top: 0.1em;
    z-index: 10;

    text-align: center;
    font-weight: bold;

    background-color: rgb(71, 108, 255);
    color: white;
}

.mainHeader .slotTimeHeader {
    position: sticky;
    top: 0.1em;
    left: 0.1em;

    background-color: rgb(71, 108, 255);
    color: white;
    font-weight: bold;
}

